html{
  font-size: 100%;
}
body {
  margin: 0;
  /*font-family: -apple-system, BlinkMacSystemFont,'Roboto', 'Oxygen', 'Segoe UI',*/
  /*'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',*/
  /*  sans-serif;*/
  font-family:'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  color: #505350;
  font-size: 0.875rem; /* соответствует 14px,  1rem=16px   */
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* Для формы авторизации и регистрации */
.input-field {
  width: 100%;
  height: 25px;
  font-size: 1rem; /* 16px */
  line-height: 1.3125rem; /* - 21px, 14px - 0,875rem,  12px - 0,75rem, 10px - 0,625rem */
  border-radius: 5px;
  border: 2px solid #0C1618;
  outline: none;
}
.input-field-error {
  border-color: #f04d2d;
  outline: none;
}
.input-field-error:focus {
  border-color: #f04d2d;
  box-shadow: 1px 1px 2px 0 #f04d2d;
  outline: none;
}

/* Для загрузчика */
.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #f04d2d;
  border-color: #f04d2d transparent #f04d2d transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

